import React from "react";
import { Component } from "react";
import { connect } from "react-redux";

import { loadPokemon } from "../actions/pokemon-actions.js";

@connect((store) => {
    console.log(store);
    return {
        pokemons: store.pokemon.pokemons
    }
})
export default class Pokemons extends Component {
    componentDidMount() {
        this.props.dispatch(loadPokemon());
    }

    pad(n, length) {
        var len = length - (''+n).length;
        return (len > 0 ? new Array(++len).join('0') : '') + n
    }

    render() {
        const pokemonsList = this.props.pokemons.map( pokemon => {
            const pokmonNumber = pokemon.url.split('/');
            console.log();
            return (
                <li key={pokemon.name}>
                    <img src={`https://assets.pokemon.com/assets/cms2/img/pokedex/detail/${this.pad(pokmonNumber[6], 3)}.png`} />
                    {pokemon.name}
                </li>
            )
        });
        return (
            <div>
                <h1>Pokemon List</h1>
                <ul>{pokemonsList}</ul>
            </div>
        )
    }
}